.container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 40px;
}

.blocks {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-self: center;
    padding: 12px 24px;
}

.blocks-item {
    width: 50px;
    height: 50px;
    font-size: 0;
    cursor: pointer;

    &+.blocks-item {
        margin-top: 24px;
    }

    &.blocks-item--moving {
        position: fixed;
        z-index: 1;
        pointer-events: none;
    }
}

.view {
    display: flex;
    flex-grow: 1;
    height: 100%;
    position: relative;
}